html {
  --color-black-1: #0e0e0e;
  --color-black-2: #1a1a1a;
  --color-black-3: #444444;
  --color-black-4: #575757;
  --color-gold: #dbb25d;
  --color-green: #4bb74d;
  --color-blue: #397cfc;
  --color-red: #c12843;
  --color-white: #ffffff;
  background-color: var(--color-black-1);
}

.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.App-logo {
  margin-top: 5vh;
  pointer-events: none;
  max-width: 80vw;
  max-height: 10vh;
  margin-bottom: 10vh;
}
