.main-menu-button {
  width: 90%;
  background-color: var(--color-black-3);
  border-radius: 0.2em;
  border-style: solid;
  border-color: var(--color-blue);
  margin-left: auto;
  margin-right: auto;
  display: flex;
  font-weight: normal;
  padding: 0%;
  font-family: inherit;
  font-size: 7vh;
  max-height: 10%;
  padding-top: 1vh;
  padding-bottom: 1vh;
}

.main-menu {
  background-color: var(--color-black-2);
  height: 100%;
}

.main-menu-button:disabled {
  opacity: 0.4;
  border-style: none;
}

.main-menu-button-container {
  display: flex;
  flex-direction: column;
  gap: 1.5vh;
  padding-top: 2vh;
}

.main-menu-footer {
  display: flex;
  position: fixed;
  font-size: 3vh;
  background-color: var(--color-black-1);
  bottom: 0;
  padding-left: 5%;
  padding-bottom: 1vh;
  padding-right: 10%;
  width: 90%;
}

.home-header-wrapper {
  background-color: var(--color-black-1);
  position: relative;
  z-index: 2;
}

.home-header {
  display: flex;
  background-color: var(--color-black-1);
  position: relative;
  justify-content: space-between;
  align-items: center;
  columns: 3;
  flex-direction: row;
  padding-top: 2vh;
  padding-bottom: 2vh;
  min-height: 7vh;
  max-width: 92vw;
  left: 4vw;
  right: 4vw;
  z-index: 3;
}

.home-header-hamburger-icon {
  height: 6vh;
  max-width: 13vw;
  color: var(--color-white);
  flex: 1;
}

.home-header-scan-icon {
  height: 6vh;
  max-width: 13vw;
  color: var(--color-white);
  flex: 1;
}

.home-header-ibcos-logo {
  height: 7vh;
  max-width: 55vw;
}

.home-header-hamburger-button {
  color: var(--color-white);
  background-color: var(--color-black-3);
  font-size: 4vh;
  margin-top: 1vh;
  border-radius: 1vh;
  width: 100%;
  border-width: 0.5vh;
  border-color: var(--color-blue);
}

#home-header-hamburger-menu {
  background-color: var(--color-black-2);
  text-justify: auto;
  position: fixed;
  border-style: solid;
  border-width: 1vh;
  border-radius: 3vh;
  border-color: var(--color-black-4);
  margin-left: 1vh;
  margin-right: 1vh;
  left: 0;
  right: 0;
  top: 12vh;
  height: 21vh;
  padding: 1vh;
  z-index: 1;
  -webkit-transition: 0.25s linear;
  -moz-transition: 0.25s linear;
  -o-transition: 0.25s linear;
  transition: 0.25s linear;
}

.hamburger-open {
  -webkit-transition: 0.25s linear;
  -moz-transition: 0.25s linear;
  -o-transition: 0.25s linear;
  transition: 0.25s linear;
  margin-top: 0vh;
  margin-bottom: 0vh;
  opacity: 1;
}
.hamburger-closed {
  margin-top: -16vh;
  pointer-events: none;
  opacity: 0;
}
.main-menu-button-icon-surround[data-disabled="true"] {
  color: var(--color-white);
}
.main-menu-button-icon-surround {
  color: var(--color-gold);
  font-size: 10vh;
  justify-self: right;
  flex: 1;
  height: 100%;
}

.main-menu-footer-text {
  flex: 1;
  border-color: var(--color-gold);
  color: var(--color-black-3);
  border-style: solid;
  border-width: 2px 0px 0px;
  color: var(--color-white);
  padding-top: 3%;
  font-size: inherit;
  text-align: right;

}

.main-menu-footer-icon {
  flex: 1%;
  border-color: var(--color-gold);
  color: var(--color-black-3);
  padding-top: 3%;
  padding-left: 1vw;
  text-align: left;
  border-style: solid;
  border-width: 2px 0px 0px;
  font-size: 8vh;
}

@media screen and (min-aspect-ratio: 5/3) {
  .main-menu-button-container {
    flex-direction: row;
    column-count: 2;
    align-items: center;
    gap: 4vh;
    justify-content: center;
    margin-top: 2vh;
    padding-top: 6vh;
    flex-wrap: wrap;
  }
  .main-menu-button {
    width: 42%;
    margin-left: 0px;
    font-size: 12vh;
    margin-right: 0px;
    border-radius: 2vh;
  }
  .main-menu-button-icon-surround {
    color: var(--color-gold);
    font-size: 19vh;
    text-align: right;
    flex: 1;
  }
  .main-menu-footer {
    padding-bottom: 3vh;
    font-size: 5vh;
  }

  .main-menu-banner {
    padding-top: 2em;
    padding-bottom: 1em;
  }

  .main-menu-banner {
    font-size: 4vh;
  }

  .main-menu-ibcos-logo {
    height: 15vh;
  }

  .main-menu-hamburger {
    height: 16vh;
    padding-top: 3vh;
  }

  .main-menu-footer-icon {
    flex: 1%;
    border-color: var(--color-gold);
    color: var(--color-black-3);
    padding-top: 3%;
    padding-left: 3vw;
    text-align: left;
    border-style: solid;
    border-width: 2px 0px 0px;
    font-size: 12vh;
  }

  .main-menu-footer-text{
    padding-right: 3vh;
  }

}

svg {
  display: block;
}

.main-menu-button-text {
  font-size: inherit;
  text-align: left;
  padding-left: 0.4em;
  flex: 9;
  font-family: inherit;
  color: var(--color-white);
}

